import React from 'react';
import Media from 'react-media';
import style from './fifth-screen.module.css';
import Header from './header/header';
import NavigationServices from '../navigationServices/navigationServices';
import Information from './infornation/information';
import messageImg from '../../img/infoImg/messages.png';
import messageImgW from '../../img/infoImg/messages.webp';

const FifthSection = ({ onOpenModal }) => (
  <section id="Messages" name="Messages" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={4} />
          </div>
        )
      }
    </Media>
    <Information onOpenModal={onOpenModal} />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={messageImgW} type="image/webp" />
            <source srcSet={messageImg} type="image/png" />
            <img className={style.img} src={messageImg} alt="Messages" />
          </picture>
        )
      }
    </Media>
  </section>
);

export default FifthSection;
