import React from 'react';
import Media from 'react-media';
import style from './information.module.css';
import Buttons from '../../buttons/buttons-dark';
import img from '../../../img/infoImg/messages.png';
import imgW from '../../../img/infoImg/messages.webp';

const Information = ({ onOpenModal }) => (
  <div className={style.wrapper}>
    <p className={style.text}>
      Мощный комплекс влияния на абонентов с обратной связью и оценкой
      эффективности. Автодозвон, Viber и SMS уведомления, E-mail рассылки и Push
      уведомления
    </p>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={imgW} type="image/webp" />
            <source srcSet={img} type="image/png" />
            <img className={style.img} src={img} alt="Messages" />
          </picture>
        )
      }
    </Media>
    <Buttons
      path="messages"
      onOpenModal={onOpenModal}
      className={style.buttons}
    />
  </div>
);

export default Information;
